table.dashboard {
  border: 1px solid black;
  background-color: white;
  margin: 20px auto;
  width: 80%;
  text-align: left;
  border-collapse: collapse;
}
table.dashboard td,
table.dashboard th {
  border: 1px solid black;
  padding: 3px 2px;
}
table.dashboard tbody td {
  font-size: 13px;
}

.table-link {
  color: black;
  text-decoration: none;
}

.table-link:hover {
  color: black;
  text-decoration: none;
  text-decoration-line: underline;
}

table.dashboard thead th {
  font-size: 15px;
  font-weight: normal;
  border: 1px solid black;
}
table.dashboard thead th:first-child {
  border-left: none;
}
