.selector {
  border: none;
  border-left: 1px solid black;
  width: 100%;
  font-size: 1rem;
  border-radius: 0px;
}

.selector > div {
  border-radius: 4px;
}

.selector-wrapper {
  display: flex;
  flex-direction: row;
  border: 1px solid gray;
  margin: 10px;
  width: 400px;
}

.selector-header {
  margin: 9px 6px 0 6px;
  text-align: center;
}
